import React, { useState, useEffect } from 'react';

const Age = () => {
    const [age, setAge] = useState();

    const tick = () => {
        const divisor = 1000 * 60 * 60 * 24 * 365.2421897; // ms in an average year
        const birthTime = new Date('1994-12-28T00:00:00');
        setAge(((Date.now() - birthTime) / divisor).toFixed(11));
    };

    useEffect(() => {
        const timer = setInterval(() => tick(), 25);
        return () => {
            clearInterval(timer);
        };
    }, []);
    return <>{age}</>;
};

const data = [
    {
        key: 'age',
        label: 'Current age',
        value: <Age />,
    },
    {
        key: 'location',
        label: 'Current city',
        value: 'SAS Nagar Mohali, PUNJAB',
    },
    {
        key: 'hometown',
        label: 'Hometown city',
        value: 'Sangrur, PUNJAB',
    },
];

export default data;
